@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');

body {
    font-family: 'Orbitron', sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    height: 100vh;
}

#canvas-space {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

}

#header{
    display: flex;
    padding: 1rem;   
}
#header > *:not(:last-child) {
    margin-right: 1rem;
}

#controls {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}
#controls.hidden *{
    display: none;
}

#charts {
    position: absolute;
    width: 30%;
    bottom: 5rem;
    left: 5rem;
}
#charts.hidden{
    display: none;
}